import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { MatIconModule } from '@angular/material/icon';
import { MatSidenav, MatSidenavModule } from '@angular/material/sidenav';
import { ActivatedRoute, NavigationStart, Router } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import * as dayjs from 'dayjs';
import * as utc from 'dayjs/plugin/utc';
import { CommonService } from 'src/app/services/common/common.service';
import { environment } from 'src/environments/environment';
import { MatDialog } from '@angular/material/dialog';
import { ChqRepairDialogComponent } from 'src/app/widgets/chq-repair-dialog/chq-repair-dialog.component';
import { filter } from 'rxjs';
dayjs.extend(utc)
@Component({
  selector: 'history',
  templateUrl: './history.component.html',
  styleUrls: [ './history.component.scss' ],
  standalone: true,
  imports: [
    WidgetsModule,
    TranslateModule,
    CommonModule,
    MatSidenavModule,
    MatIconModule,
  ],
})
export class HistoryComponent{
  /**
   * Creates an instance of HistoryComponent.
   * @constructor
   * @param {Router} router
   */
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private commonService: CommonService,
    private dialog: MatDialog,
  ) {
    this.router.events.pipe(
      filter(event => event instanceof NavigationStart)
    ).subscribe({
      next: (data) =>{
        if(this.router.url.indexOf('?') !==-1 && this.router.url.indexOf('history=open')!==-1){
          this.isMenuOpen = true;
          this.toggleExpand.emit(this.isMenuOpen)
        }
      }
    })
    this.route.params.subscribe({
      next: (data) =>{
        if(this.router.url.indexOf('?') !==-1 && this.router.url.indexOf('history=open')!==-1){
          this.isMenuOpen = true;
          this.toggleExpand.emit(this.isMenuOpen)
        }
      }
    })

    this.currencySymbol = this.commonService.getCurrencySymbol()
  }
  /**
   * constructor for HistoryComponent
   */
  @ViewChild('sidenav') sidenav: MatSidenav;
  @Output() toggleExpand:EventEmitter<boolean> = new EventEmitter()
  @Input() public history = [];
  @Input() isMenuOpen = false;
  @Input() isLoading = false;
  /**
   * Show/hide
   */
  close(): void {
    this.isMenuOpen = !this.isMenuOpen;
    this.toggleExpand.emit(this.isMenuOpen)
  }
  currencySymbol = ''
  public viewMessage: ChqWidgetsButtonModel = {
    label: 'View message',
    icon: 'show',
    type: 'outline',
  };
  
  /**
   * Returns class name according to status
   */
  getStatusClasses(status:string):string {
    switch (status) {
    case 'Draft':
      return 'status-draft';
    case 'Pending Approval':
      return 'status-pending';
    case 'Upload photos':
      return 'status-upload';
    case 'Repair estimate':
      return 'status-repair-estimate';
    default:
      return '';
    }
  }

  /**
   * download pdf history
   */
  downloadPdfHistory(referenceId: number): void {
    this.commonService.showLoading();
    this.commonService.blobObservable1(`${environment.caseMgmtUrl}/api/casehistory/report/${referenceId}`).subscribe({
      next: (data: any) => {
        window.open(data.fileURL, '_blank');
        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      },
    });
  }
  /**
   * show image dialog
   */
  showImageDialog(blobUrls: string[], name: string): void {
    const gallery = [];

    blobUrls.forEach((url) => {
      let title = '';
      let imageUrl = url;

      if (name.indexOf('photo') !== -1 || name.indexOf('document') !== -1) {
        title = url.substring(0, url.indexOf('|')).trim();
        imageUrl = url.substring(url.indexOf('|') + 1);
      }

      gallery.push({
        image: imageUrl,
        thumbImage: imageUrl,
        //alt: alt,
        title: title
      });
    });

    const data = { 'imageGallery': true, 'gallery': gallery };
    this.dialog.open(ChqRepairDialogComponent, {
      data:
          data
    });
  }

  /**
   * show comment dialog
   */
  showCommentDialog(comment: string, status: string, email?:null): void {
    const data = { 'comment': comment, 'status': status, 'email': email };
    this.dialog.open(ChqRepairDialogComponent, {
      data:
          data
    });
  }


}
